import { createOktaAuthCore } from '../core/factory.js';
import { mixinMyAccount } from './mixin.js';

function createOktaAuthMyAccount(StorageManagerConstructor, OptionsConstructor, TransactionManager) {
    const Core = createOktaAuthCore(StorageManagerConstructor, OptionsConstructor, TransactionManager);
    const WithMyAccount = mixinMyAccount(Core);
    return WithMyAccount;
}

export { createOktaAuthMyAccount };

